.App {
  display: flex;
  font-family: Arial, sans-serif;
}

.sidebar {
  width: 200px;
  padding: 20px;
  background-color: #f0f0f0;
}

.song-list {
  list-style: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.song-list li {
  margin-bottom: 10px;
  padding: 8px;
  background-color: #ddd;
  border-radius: 4px;
  transition: background-color 0.9s ease;
}

.song-list li:hover {
  background-color: #ccc;
}
h2{
  
}
.main-content {
  flex-grow: 1;
  padding: 20px;
}

.song-display {
  text-align: center;
}

.chords,
.lyrics {
  margin-top: 20px;
  font-size: 20px;
  white-space: pre-line;
}
